.sidebar {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    flex: 0.35;
}

.sidebar_header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background: #ededed;
    padding: 20px;
}
.sidebar_headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}
.sidebar_headerRight>.MuiSvgIcon-root{
    margin-right: 2vw;
    font-size: 24px !important;
}

.sidebar_search {
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}
.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.sidebar_searchContainer > .MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}

.searchResults {
    width: 100%;
    height: 100px;
}

.sidebar_searchContainer > input{
    border: none;
    outline-style: none;
    margin-left: 10px;
    width: 100%;
}
.sidebar_chats{
    flex: 1;
    background-color: white;
    overflow-y: scroll;
}