.sidebarChat{
    display: flex;
    flex-direction: row;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
}
.sidebarChat:hover{
    background-color: #ebebeb;
}
.sidebarChat_info{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    margin-left: 15px;
}

.sidebarChat_info > .msgtime {
    float: right;
}

.contactname {
    width: 100%;
    font-size: 20px;
    font-weight: bold;
    color: black;
}
.msgtext {
    width: 100%;
    font-size: 14px;
    margin-top: 3px;
    color: rgba(0, 0, 0, 0.8);
}
.msgtime {
    font-size: 12px;
    float: right;
    margin-right: 10px;
    color: rgba(0, 0, 0, 0.45);
    white-space: nowrap;
}