.chat {
    display: flex;
    flex-direction: column;
    height: 100%;
    flex: 0.65;
}

.chat_header {
    padding: 20px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid lightgray;
    font-weight: bold;
    gap: 10px;
}
.chat_header > p{
    font-size: 12px;
    color: rgba(0, 0, 0, 0.45);
}
.chat_headerinfo {
    flex: 1;
    padding-left: 20px;
    font-size: 20px;
    font-weight: bold;
}

.chat_headerinfo > h3 {
    margin-bottom: 3px;
    font-weight: 500;
}

.chat_headerinfo > p {
    color: gray;
    font-size: 12px;
    font-weight: normal;
}

.chat_body {
    flex: 1;
    background-image: url(https://user-images.githubusercontent.com/15075759/28719144-86dc0f70-73b1-11e7-911d-60d70fcded21.png);
    background-repeat: repeat;
    background-position: center;
    padding: 30px;
    overflow-y: scroll;
}

.chat_msg{
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    max-width: 50%;
    border-radius: 10px;
    background-color: #ffffff;
    margin-bottom: 30px;
}

.chat_receiver{
    margin-left: auto;
    position: relative;
    font-size: 16px;
    padding: 10px;
    width: fit-content;
    border-radius: 10px;
    background-color: #aaf2fa;
    margin-bottom: 30px;
}

.chat_timestamp{
    margin-left: 10px;
    font-size: xx-small;
}

.chat_name{
    position: absolute;
    top: -15px;
    font-weight: 800;
    font-size: xx-small;
}

.chat_footer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 62px;
    border-top: 1px solid lightgray;
}

.chat_footer > form {
    flex: 1;
    display: flex;
}

.chat_footer > form > input{
    flex: 1;
    border-radius: 30px;
    padding: 10px;
    border: none;
}

.chat_footer > form > button {
    display: none;
}

.chat_footer > .MuiSvgIcon-root{
    padding: 10px;
    color: gray;
}