.app {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: #dadbd3;
}
.app_body {
  display: flex;
  background-color: #ededed;
  margin-top: -50px;
  height: 90vh;
  width: 90vw;
  box-shadow: -1px 4px 20px -6px rgba(0, 0, 0, 0.75);
}

.placeholder {
  display: flex;
  flex: 0.65;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.45);
  gap: 10px;
}
.placeholder > span {
  font-size: 32px;
  color: #525252;
}

.chatPlaceholder {
  width: 240px;
  height: 240px;
  border-radius: 50%;
  object-fit: contain;
}