.Container {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #0a0e11;
    height: 100vh;
    color: white;
}

.header {
    color: white;
    width: 100%;
    font-weight: bold;
    background-color: #52e9fa;
    padding: 50px 50px 140px;
    font-size: 14px;
}

.cardView {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 30px 50px;
    margin-left: auto;
    margin-right: auto;
    margin-top: -80px;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    gap: 40px;
    flex-wrap: wrap;
}

.cardView > img {
    width: 210px;
    height: 210px;
}

.instructions {
    padding: 20px;
    font-size: 16px;
    color: #525252;
}

.instructions > span {
    font-size: 24px;
    
}

.instructions > ol {
    margin: 40px 0;
}

.instructions > ol > li {
    margin: 15px 0;
}